body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.searchBarContainer {
  position: fixed;
  height: 40px;
  width: 70%;
  border: 1px solid black;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.searchBarLabel {
  margin: 10px;
}

.leftContainer,
.rightContainer {
  float: left;
  overflow: scroll;
}

.leftContainer {
  margin-top: 40px;
  width: 70%;
  height: 95vh;
  overflow-y: scroll;
}

.rightContainer {
  height: 100vh;
}

.text {
  width: 70%;
  margin: 15px auto;
}

pre {
  font-size: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.rightContainer {
  width: 30%;
}

.createHighlightContainer {
  background-color: #e8e8e8;
}

.fullPageContainer {
  position: fixed;
  width: 100%;
  height: 100%;
}

.modalContainer {
  width: 600px;
  max-width: 100%;
  height: 500px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #e8e8e8;
  border: 2px solid black;
  border-radius: 2px;
}

.addForm {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 70%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: 10px auto;
}

.phraseInfo {
  overflow: hidden;
}

.signature {
  margin-top: 20px;
}

